@import "https://fonts.googleapis.com/css2?family=Inter:wght@200;300;400;500;600;700&display=swap";
body {
  font-family: Inter, sans-serif;
  font-size: 16px;
  font-weight: 400;
}

* {
  box-sizing: border-box;
}

.swiper-pagination-bullets {
  margin-top: 50px;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
  overflow: hidden;
}

.switch {
  width: 46px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.switch input {
  display: none;
}

.slider {
  cursor: pointer;
  background-color: #cecdcd;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: 7px 5px 15px rgba(160, 158, 160, .6);
}

.slider:before {
  content: "";
  height: 18px;
  width: 18px;
  background-color: #fff;
  transition: all .4s;
  position: absolute;
  bottom: 1px;
  left: 1px;
}

input:checked + .slider {
  background-color: #9bb537;
}

input:focus + .slider {
  box-shadow: 0 0 1px #9bb537;
}

input:checked + .slider:before {
  transform: translateX(26px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.main-header {
  justify-content: space-between;
  align-items: center;
  margin-top: 45px;
  margin-bottom: 100px;
  padding: 0 100px;
  display: flex;
}

.main-logo img {
  width: 93px;
}

.main-nav a {
  color: #000;
  font-size: 12px;
  text-decoration: none;
}

.main-nav a:first-child {
  margin-right: 0;
}

.first-screen__left-column {
  flex: 1 0 40%;
}

.first-screen__left-column p {
  max-width: 408px;
  font-weight: 400;
  line-height: 24px;
}

.first-screen__btn {
  background-color: #fff;
  border: 1px solid #9bb537;
  border-radius: 15px;
  margin-top: 10px;
  padding: 14px 28px;
  font-size: 14px;
  font-weight: 500;
}

.first-screen {
  justify-content: space-between;
  align-items: center;
  padding-left: 100px;
  display: flex;
}

.first-screen h1 {
  text-transform: uppercase;
  margin-bottom: 0;
  font-size: 36px;
  font-weight: 600;
  line-height: 43px;
}

.first-screen span {
  color: #9bb537;
  font-style: italic;
  font-weight: 400;
}

.first-screen__right-column, .slide-img {
  position: relative;
}

.slide-img img {
  width: 100%;
}

.slide__img-wrar {
  display: flex;
}

.swiper {
  width: 870px;
  height: 470px;
}

.swiper .swiper-pagination {
  width: initial !important;
  bottom: 0 !important;
  left: 36% !important;
}

.swiper .swiper-pagination-bullet {
  width: 5px;
  height: 5px;
  opacity: .8;
  margin: 0 2px !important;
}

.swiper .swiper-pagination-bullet-active {
  background-color: #9bb537;
}

.first {
  width: 137px;
  height: 442px;
  background-position: center;
  background-size: cover;
  border-radius: 15px;
  flex: 1 0 auto;
  margin-right: 15px;
}

.second {
  width: 350px;
  height: 442px;
  background-color: #1e220d;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  border-radius: 15px;
  flex: 1 0 auto;
  margin-right: 15px;
}

.third {
  width: 350px;
  height: 442px;
  background-color: #1e220d;
  background-position: center;
  background-size: 150%;
  border-radius: 15px;
  flex: 1 0 auto;
}

.slide-text {
  text-align: center;
  position: relative;
  bottom: 60px;
  left: -12%;
}

a.active {
  color: #9bb537;
}

.active {
  color: #9bb537 !important;
}

.slide-text h2 {
  color: rgba(248, 248, 248, .6);
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 500;
  line-height: 0;
}

.slide-text p {
  color: rgba(248, 248, 248, .2);
  font-size: 12px;
  line-height: 25px;
}

.second-screen {
  margin-top: 100px;
  padding: 0 100px;
  display: flex;
}

.second-screen__left-column {
  max-width: 235px;
  flex: 1 0 21%;
}

.second-screen__right-column {
  margin-left: 64px;
}

.right-column__top {
  flex-direction: column;
  display: flex;
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-thumb {
  height: 10%;
  background-color: rgba(2, 2, 2, .2);
  border-radius: 12px;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #9bb537;
}

.left-column__categories-list {
  height: 416px;
  flex-direction: column;
  align-items: start;
  margin-bottom: 64px;
  padding: 0;
  list-style-type: none;
  display: flex;
  overflow-x: hidden;
  overflow-y: scroll;
}

.left-column__categories-link {
  color: rgba(2, 2, 2, .3);
  cursor: pointer;
  margin-bottom: 24px;
  font-weight: 500;
  line-height: 20px;
}

.left-column__categories-link:hover {
  color: #9bb537;
}

.left-colum__all-categories-btn {
  color: rgba(2, 2, 2, .5);
  cursor: pointer;
  background-color: #fff;
  border: 1px solid rgba(2, 2, 2, .2);
  border-radius: 15px;
  margin-bottom: 42px;
  padding: 14px 28px;
  font-size: 18px;
  font-weight: 500;
  transition: all .5s;
}

.left-colum__all-categories-btn:hover {
  color: #fff;
  background-color: #9bb537;
  border-color: #9bb537;
}

.left-column__bottom h2 {
  text-transform: uppercase;
  font-size: 24px;
  font-weight: 600;
  line-height: 28px;
}

.popular-recipes__card {
  cursor: pointer;
  align-items: center;
  margin-bottom: 32px;
  display: flex;
}

.popular-recipes__card:has(:hover) .card-img img {
  transform: scale(1.3);
}

.popular-recipes__card:has(:hover) .card-title h3 {
  color: #9bb537;
}

.card-img {
  margin-right: 16px;
}

.card-img img {
  width: 64px;
  height: 64px;
  border-radius: 15px;
  transition: all .5s;
}

.card-wrap-text {
  flex-direction: column;
  display: flex;
}

.card-title h3 {
  text-transform: uppercase;
  margin: 0 0 8px;
  font-size: 14px;
  transition: all .5s;
}

.card-description {
  -webkit-line-clamp: 2;
  height: 2.5em;
  -webkit-box-orient: vertical;
  font-size: 10px;
  line-height: 1.3em;
  display: -webkit-box;
  overflow: hidden;
}

.wrap-inputs {
  display: flex;
}

.right-column__recepies-cards {
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 15px;
  display: flex;
}

.recipe-card {
  width: 250px;
  margin-bottom: 16px;
}

.wrap-input {
  flex-direction: column;
  display: flex;
}

.recipe-card-img {
  width: 250px;
  height: 287px;
  background-position: center;
  background-size: cover;
  border-radius: 15px;
  padding: 16px;
  position: relative;
}

.recipe-card-information {
  color: #fff;
  width: 218px;
  position: absolute;
  bottom: 20px;
}

.recipe-card-title {
  text-transform: uppercase;
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 600;
}

.recipe-card-description {
  -webkit-line-clamp: 2;
  height: 2em;
  -webkit-box-orient: vertical;
  margin-bottom: 16px;
  font-size: 12px;
  line-height: 1em;
  display: -webkit-box;
  overflow: hidden;
}

.favorite-heart {
  z-index: 1;
  cursor: pointer;
  opacity: .7;
  font-family: "Font Awesome 5 Free";
  font-size: 25px;
  font-weight: 200;
  transition: all .5s;
  position: absolute;
  right: 18px;
}

.favorite-heart:hover {
  transform: scale(1.3);
}

.wrap-rate {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.see-recipe {
  color: #fff;
  cursor: pointer;
  background-color: #9bb537;
  border: none;
  border-radius: 10px;
  padding: 10px 15px;
  font-size: 12px;
  font-weight: 500;
  transition: all .5s;
}

.see-recipe:hover {
  transform: scale(1.1);
}

.stars-outer {
  display: inline-block;
  position: relative;
}

.stars-outer:before {
  content: "";
  color: rgba(255, 255, 255, .7);
  font-family: "Font Awesome 5 Free";
  font-size: 14px;
  font-weight: 900;
}

.stars-inner {
  white-space: nowrap;
  width: 0;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.stars-inner:before {
  content: "";
  color: #eea10c;
  font-family: "Font Awesome 5 Free";
  font-size: 14px;
  font-weight: 900;
}

.stars-rating span {
  font-size: 14px;
}

